@use '../utils/mixins' as *;
@use '../utils/vars' as *;

.our-expertise {
  background-color: $text-color;
  border-radius: 30px;
  padding: 72px 20px 16px;
  text-align: center;

  @include for-tablet {
    padding: 128px 41px 56px;
  }

  @include for-desktop {
    padding: 128px 41px 62px;
  }

  @include for-large {
    padding: 128px 41px 83px;
  }

  @include page-grid;

  &__title {
    font-family: $font-primary;
    font-size: 44px;
    font-weight: 600;
    line-height: 52.8px;
    text-align: center;
    color: #253757;
    grid-column: span 2;
    margin-bottom: 56px;

    @include for-tablet {
      grid-column: span 6;
      margin-bottom: 64px;
    }

    @include for-desktop {
      grid-column: span 12;
    }

    @include for-large {
      grid-column: span 12;
    }

    &--secondary {
      font-family: $font-primary;
      font-size: 20px;
      font-weight: 600;
      line-height: 28px;
      text-align: center;
      margin-bottom: 16px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 56px;
    grid-column: span 2;

    @include for-tablet {
      margin-bottom: 72px;
      grid-column: 2 / 6;
    }

    &:first-of-type {
      @include for-desktop {
        grid-column: 1 / 4;
      }
    }

    &:nth-of-type(2) {
      @include for-desktop {
        grid-column: 5 / 9;
      }
    }

    &:last-of-type {
      @include for-desktop {
        grid-column: 10 / 13;
      }
    }
  }

  &__picture {
    margin-bottom: 32px;
    border-radius: 17px;
    width: 166px;
    height: 166px;

    @include for-desktop {
      margin-bottom: 40px;
    }
  }
}
